import Notiflix from 'notiflix';

export default class Message {
  constructor() {}
  //   
  info(message) {
    Notiflix.Notify.info(message);
  }
  //    
  failure(message) {
    Notiflix.Notify.failure(message);
    console.log(message);
  }
  //     
  succes(message) {
    Notiflix.Notify.success(message);
    console.log(message);
  }
}
